
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import CountDown from './count-down.vue'
import Data from "./data.js";
import Scroll from "./scroll.vue";

const url = 'https://wx.sharkmeida.cn/distribution/info/95ce4038a19b49f5bfedb8722eef9ecf'
export default {
name: "info",
components: {
Group,
Cell,
Loading,
Datetime,
XInput,
XNumber,
XSwitch,
XTextarea,
Scroll,
CountDown
},
data() {
return {
list: [{
img: 'https://wx.sharkmeida.cn/imgs/82ef949d-3af8-466a-ad94-4edc1d215df5.jpg',
text: '2sdsjdksjkdjksjkajkjdkhsajdhkajdhjshajhjdjqwjhjqhjhwjqhbnbbbsah'
}],
formD: Data,
isLoading: true,
musicUrl:""
};
},
props: {
form: {
type: Object
}
},
mounted(){
this.query();
},
methods:{
stop(){
var audio = document.getElementById("audio2");
if(audio == null)
return
audio.pause();
},
play(item){
var audio = document.getElementById("audio2");
if(audio == null)
return
audio.pause();
audio.load();
audio.play();
},
query(){
if(!!this.form){
this.formD = this.form;
return
}
this.$http.get(url).then(({data}) => {
if(data.code == '0000'){
this.formD = data.distribution
this.formD.music = JSON.parse(this.formD.music)
var thumbnail = data.distribution.thumbnail
var currentUrl =  'https://wx.sharkmeida.cn/dist/index.html'

this.$http.post('https://wx.sharkmeida.cn/api/wxpay/initwxjs?url='+currentUrl).then(function (data, status) {
wx.config({
debug: true, // ,apialertpclogpc
appId: data.data.result.data.appId, // 
timestamp: data.data.result.data.timestamp, // 
nonceStr: data.data.result.data.nonceStr, // 
signature: data.data.result.data.signature,// 
jsApiList: ['onMenuShareTimeline','onMenuShareAppMessage'] 
});

wx.ready(function(){


wx.error(function(res){

console.log(res);
});

wx.checkJsApi({
jsApiList: ['onMenuShareTimeline','onMenuShareAppMessage'], // JSJS2,
success: function(res) {


console.log('checkJsApi'+ res);
}
});

var shareParam = {
title: '', // 
desc: '', // 
link: currentUrl, // JS
imgUrl: thumbnail, // 


trigger: function (res) {
console.log('');
},
success: function (res) {
console.log('');
},
cancel: function (res) {
console.log('');
},
fail: function (res) {
}
};

wx.onMenuShareTimeline(shareParam);
wx.onMenuShareAppMessage(shareParam);

});

})
}
})
}
}
};



